import React, {Component} from 'react'
import {Cell, Grid, List, ListItem, ListItemContent} from "react-mdl";

class Contact extends Component {
    render() {
        return (
            <div className="contact-body">
                <Grid className="contact-grid">
                    <Cell col={6}>
                        <h2>Abdumutal Abdusamatov</h2>
                        <img
                            src="./images/avatar.png"
                            alt="Avatar"
                            style={{height: '250px'}}
                        />
                        <p style={{width: '75%', margin: 'auto', paddingTop: '1em'}}>Lorem Ipsum doler.Lorem Ipsum
                            doler.Lorem Ipsum doler.Lorem Ipsum doler.Lorem Ipsum
                            doler.Lorem Ipsum doler.Lorem Ipsum doler.Lorem Ipsum doler.Lorem Ipsum doler.Lorem Ipsum
                            doler.Lorem Ipsum doler.Lorem Ipsum doler.</p>
                    </Cell>
                    <Cell col={6}>
                        <h2>Contact me</h2>
                        <hr/>
                        <div className="contact-list">
                            <List>
                                <ListItem>
                                    <ListItemContent style={{fontSize:'20px'}}>
                                        <i aria-hidden="true" className="fa fa-phone-square"/>
                                        (+998 99) 897-05-97
                                    </ListItemContent>
                                </ListItem>
                                <ListItem>
                                    <ListItemContent style={{fontSize:'20px'}}>
                                        <i aria-hidden="true" className="fa fa-envelope-square"/>
                                        a.abdusamatov@student.inha.uz
                                    </ListItemContent>
                                </ListItem>
                                <ListItem>
                                    <ListItemContent style={{fontSize:'20px'}}>
                                        <i aria-hidden="true" className="fa fa-github-square"/>
                                        https://github.com/mebungler
                                    </ListItemContent>
                                </ListItem>
                            </List>
                        </div>
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Contact;


