import React, {Component} from 'react'
import {Button, Card, CardActions, CardMenu, CardText, CardTitle, Cell, Grid, IconButton, Tab, Tabs} from "react-mdl";

class Projects extends Component {
    constructor(props) {
        super(props);
        this.state = {activeTab: 0};

    }

    toggleCategories() {
        if (this.state.activeTab === 0) {
            return (
                <div className="projects-grid">
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle className="card-title" style={{
                            color: '#fff',
                            height: '176px',
                            background: 'url(./images/portfolio-app.png) no-repeat 0 0',
                            backgroundSize: 'cover',
                        }}/>
                        <CardText>
                            My first react App
                        </CardText>
                        <CardActions border>
                            <Button colored>GitHub</Button>
                            <Button colored>BitBucket</Button>
                            <Button colored>CodePen</Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton name="share"/>
                        </CardMenu>
                    </Card>
                </div>
            )
        } else if (this.state.activeTab === 1) {
            return (
                <div className="projects-grid">
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{
                            color: '#fff',
                            height: '176px',
                            background: 'url(./images/go-telegram-bot.jpg) no-repeat 0 0' ,
                            backgroundSize:'100% 100%'
                        }}/>
                        <CardText>
                            My first Telegram bot in Go using <a
                            href="https://github.com/go-telegram-bot-api/telegram-bot-api">"go-telegram-bot-api"</a> package
                        </CardText>
                        <CardActions border>
                            <Button colored>GitHub</Button>
                            <Button colored>BitBucket</Button>
                            <Button colored>CodePen</Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton name="share"/>
                        </CardMenu>
                    </Card>
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{
                            color: '#fff',
                            height: '176px',
                            background: 'url(./images/admin-app.png) no-repeat 0 0',
                            backgroundSize:'100% 100%'
                        }}/>
                        <CardText>
                            Admin web-site for Telegram bot created using Go
                        </CardText>
                        <CardActions border>
                            <Button colored>GitHub</Button>
                            <Button colored>BitBucket</Button>
                            <Button colored>CodePen</Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton name="share"/>
                        </CardMenu>
                    </Card>
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{
                            color: '#fff',
                            height: '176px',
                            background: 'url(./images/game-server-app.jpg) no-repeat 0 0',
                            backgroundSize:'100% 100%'
                        }}/>
                        <CardText>
                            Interaction back-end for Chess Game (Unity) made with Go
                        </CardText>
                        <CardActions border>
                            <Button colored>GitHub</Button>
                            <Button colored>BitBucket</Button>
                            <Button colored>CodePen</Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton name="share"/>
                        </CardMenu>
                    </Card>
                </div>
            )
        } else if (this.state.activeTab === 2) {
            return (
                <div className="projects-grid">
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{
                            color: '#fff',
                            height: '176px',
                            background: 'url(./images/wink.png) no-repeat 0 0' ,
                            backgroundSize:'100% 100%'
                        }}/>
                        <CardText>
                            Chat application created with WPF
                        </CardText>
                        <CardActions border>
                            <Button colored>GitHub</Button>
                            <Button colored>BitBucket</Button>
                            <Button colored>CodePen</Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton name="share"/>
                        </CardMenu>
                    </Card>
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{
                            color: '#fff',
                            height: '176px',
                            background: 'url(./images/binary-fun.png) no-repeat 0 0',
                            backgroundSize:'100% 100%'
                        }}/>
                        <CardText>
                            Binary fun game created with WPF
                        </CardText>
                        <CardActions border>
                            <Button colored>GitHub</Button>
                            <Button colored>BitBucket</Button>
                            <Button colored>CodePen</Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton name="share"/>
                        </CardMenu>
                    </Card>
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle style={{
                            color: '#fff',
                            height: '176px',
                            background: 'url(./images/Indigo-Logo.ico) no-repeat 0 0',
                            backgroundSize:'100% 100%'
                        }}/>
                        <CardText>
                            Indigo Music Player crated with WPF
                        </CardText>
                        <CardActions border>
                            <Button colored>GitHub</Button>
                            <Button colored>BitBucket</Button>
                            <Button colored>CodePen</Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton name="share"/>
                        </CardMenu>
                    </Card>
                </div>
            )
        } else if (this.state.activeTab === 3) {
            return (
                <div className="projects-grid">
                    <Card shadow={5} style={{minWidth: '450', margin: 'auto'}}>
                        <CardTitle className="card-title" style={{
                            color: '#fff',
                            height: '176px',
                            background: 'url(./images/chess.png) no-repeat 0 0',
                            backgroundSize: 'cover',
                        }}/>
                        <CardText>
                            Chess Game. Coming soon on PlayStore.
                        </CardText>
                        <CardActions border>
                            <Button colored>GitHub</Button>
                            <Button colored>BitBucket</Button>
                            <Button colored>CodePen</Button>
                        </CardActions>
                        <CardMenu style={{color: '#fff'}}>
                            <IconButton name="share"/>
                        </CardMenu>
                    </Card>
                </div>
            )
        }
    }

    render() {
        return (
            <div className="category-tabs">
                <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({activeTab: tabId})} ripple>
                    <Tab>React</Tab>
                    <Tab>Go</Tab>
                    <Tab>C#</Tab>
                    <Tab>Unity</Tab>
                </Tabs>
                <Grid>
                    <Cell col={12}>
                        <div className="content">
                            {this.toggleCategories()}
                        </div>
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Projects;


