import React, {Component} from 'react'
import {Cell, Grid} from "react-mdl";
import Education from './education'
import Experience from "./experience";
import Skill from "./skills";

class Resume extends Component {
    render() {
        return (
            <div>
                <Grid>
                    <Cell col={4}>
                        <div style={{textAlign: 'center'}}>
                            <img
                                src="./images/avatar.png"
                                alt="Avatar"
                                style={{height: '200px'}}
                            />
                        </div>
                        <h2 style={{paddingTop: '2em'}}>
                            Abdumutal Abdusamatov
                        </h2>
                        <h4 style={{color: 'grey'}}>
                            Software Programmer
                        </h4>
                        <hr style={{
                            borderTop: '3px solid #833fb2'
                            , width: '50%'
                        }}
                        />
                        <p>
                            I am sophomore student at Inha University in Tashkent. I have developed this website for
                            learning purpose only.
                        </p>
                        <hr style={{
                            borderTop: '3px solid #833fb2'
                            , width: '50%'
                        }}
                        />
                        <h5>Address</h5>
                        <p>9, Ziyolilar str., M.Ulugbek district, Tashkent city</p>
                        <h5>Phone</h5>
                        <p>(+99899) 8970597</p>
                    </Cell>
                    <Cell col={8} className="resume-right-col">
                        <hr style={{
                            borderTop: '3px solid #f55a66'
                            , width: '100%'
                        }}/>
                        <h2>Education</h2>
                        <Education
                            startYear={2016}
                            endYear={2020}
                            schoolName={'Inha University in Tashkent'}
                            schoolDescription={'Office :\t9, Ziyolilar str., M.Ulugbek district, Tashkent city\n' +
                            'Phone :\t+998 71 289-99-99\n' +
                            'Fax :\t+998 71 269-00-58\n' +
                            'Email :\tinfo@inha.uz\n' +
                            'Website :\twww.iut.uz, www.inha.uz'}
                        />
                        <hr style={{
                            borderTop: '3px solid #f55a66'
                            , width: '100%'
                        }}/>
                        <h4>Experience</h4>
                        <Experience
                            startYear={2016}
                            endYear={2018}
                            jobName={'Software Developer at "Vronica"'}
                            jobDescription={'Desktop Application developer, Full-stack web developer, Game developer. Link: https://vronicasoft.com'}
                        />
                        <hr style={{
                            borderTop: '3px solid #f55a66'
                            , width: '100%'
                        }}/>
                        <h4>Skills</h4>
                        <Skill
                            skill={'C#'}
                            progress={'50'}
                        />
                        <Skill
                            skill={'Go'}
                            progress={'30'}
                        />
                        <Skill
                            skill={'React'}
                            progress={'5'}
                        />
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Resume;


