import React, {Component} from 'react'

class About extends Component {
    render() {
        return (
            <div style={{textAlign: 'center',padding:'30px,'}}>
                <h1>
                    AboutMe
                </h1>
                <p style={{margin:'50px',fontSize:'30px',lineHeight:'50px'}}>
                    As Desktop application developer, I have developed strong programming and problem solving skills.
                    For example,
                    one of my main duties is to prepare Tools for VR applications. You may checkout my projects from
                    https://github.com/mebungler.
                    I enjoy being challenged and engaging with projects that require me to work outside my comfort and
                    knowledge set, as continuing to learn new languages and development techniques are important to me.
                </p>
            </div>
        )
    }
}

export default About;


