import React, {Component} from 'react'
import {Cell, Grid} from "react-mdl";

class Landing extends Component {
    render() {
        return (
            <div style={{width: '100%', margin: 'auto'}}>
                <Grid className="landing-grid">
                    <Cell col={12}>
                        <img
                            src="./images/avatar.png"
                            alt="Avatar"
                            className="avatar-image"
                        />
                        <div className="banner-text">
                            <h1>Software Developer</h1>
                            <hr/>
                            <p>
                                HTML/CSS | Bootstrap | Javascript | jQuery | React | C# | Go | Unity3D | WPF
                            </p>
                            <div className="social-links">
                                <a href="http://google.com" rel="noopener noreferrer"  target="_blank">
                                    <i className="fa fa-linkedin-square" area-hidden="true" />
                                </a>
                                <a href="http://google.com" rel="noopener noreferrer"  target="_blank">
                                    <i className="fa fa-facebook-square" area-hidden="true" />
                                </a>
                                <a href="http://google.com" rel="noopener noreferrer"  target="_blank">
                                    <i className="fa fa-instagram" area-hidden="true" />
                                </a>
                                <a href="http://google.com" rel="noopener noreferrer"  target="_blank">
                                    <i className="fa fa-github-square" area-hidden="true" />
                                </a>
                            </div>
                        </div>
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Landing;


